<template>
  <div class="home">
    <Header/>
    <Content/>
    <div v-html="homeContext.html_context" />
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import axios from 'axios'
import Header from './Header'
import Content from './Content'
// import * as api from '../lib/api'

export default {
  name: 'Home',
  components: {
    Header,
    Content
  },
  computed: {
    ...mapGetters([
      'homeContext',
      'headerNav'
    ])
  },
  created() {
    axios.get("https://sniweb.shouting.feedia.co/php/GetHomePage.php", {
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(((res) => {
      this.actionUpdateContext(res.data);
      console.log(this.homeContext);
    }));

    axios.get("https://sniweb.shouting.feedia.co/php/GetNav.php", {
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(((res) => {
      this.actionHeaderNav(res.data);
      console.log(this.headerNav);
    }));
  },
  methods: {
    ...mapActions([
      'actionUpdateContext',
      'actionHeaderNav'
    ])
  }
}
</script>

